import { Component } from '@angular/core';
import { ViewController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import {   FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';

@IonicPage()
@Component({
  selector: 'page-conferencia-create',
  templateUrl: 'conferencia-create.html',
})
export class ConferenciaCreatePage {
  public form                  : FormGroup;
  username: string = "";
  public nidevento                 : any;
  public idevento                 : any;
  public nombre                 : any;
  public conferencista                 : any;
  public fechaini		     = (new Date()).toISOString();
  public fechafin		    = (new Date()).toISOString();

  public nomax		     : any;
  public costo		     : any;
  idempresa:any;
  public estado	     : any;
  private _ID                  : String;

  public pageTitle           	: string;
  createdCode = null;
  scannedCode = null;

  isDisablednit :any;
  constructor( public storage: Storage,  public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,  
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController, 
          public events: Events)
  {
    this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value
    });
     this.form = this._FB.group({
        nombre     :  ['', Validators.required],
        conferencista    :  ['', Validators.required],
        fechaini     : ['', Validators.required],
        fechafin     : ['', Validators.required],
       
        nomax     : ['', Validators.required],
        costo     : ['', Validators.required],
        estado    : ['', Validators.required]
      
     });

  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  }); 
  }

  ionViewDidLoad() : void
  {this.idevento=this.navParams.data.record2.idevento;
    this.nidevento=this.navParams.data.record2.nidevento;
    if(this.navParams.get("record")==null)
    {
       
      this.translateService.get('ITEM_CREATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
      
      this.isDisablednit =true
    }
    else {
       this._ID 				=	this.navParams.data.record._id;
       //this.idevento		=	this.navParams.data.record.idevento;
       this.nombre		=	this.navParams.data.record.nombre;
       this.conferencista		=	this.navParams.data.record.conferencista;
       
       this.fechaini		=	this.navParams.data.record.fechaini;
   
       this.fechafin		=	this.navParams.data.record.fechafin;
       this.costo		=	this.navParams.data.record.costo;
      
       this.nomax		=	this.navParams.data.record.nomax;
       this.estado		=	this.navParams.data.record.estado;
       this.isDisablednit =false
       this.translateService.get('ITEM_UPDATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    

    }
  }

  
  
formatAMPM(date) {
  var hours = date[0];
  var minutes = date[1];
  var ampm = hours >= 12 ? 'pm' : 'am';
  hours = hours % 12;
  hours = hours ? hours : 12;
  minutes = minutes < 10 ? '0'+minutes : minutes;
  var strTime = hours + ':' + minutes + ' ' + ampm;
  return strTime;
}

  manageGallery() : void
  {
   
  let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }
   

    

     let   options       : any	     = { 
       idempresa:this.idempresa,
      idevento : this.idevento,
     nombre : this.form.controls['nombre'].value,
     conferencista : this.form.controls['conferencista'].value,
     fechaini : this.form.controls['fechaini'].value,
     fechafin : this.form.controls['fechafin'].value,
     costo : this.form.controls['costo'].value,
     nomax : this.form.controls['nomax'].value,
     estado : this.form.controls['estado'].value,
     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' Conferencias '}
    };


     if(this.navParams.get("record")==null)
     {  
          if(options){
            this.authService.createReg('',options,'conferencias').then((result) => {
              if (!this.form.valid) { return; }
              console.log(result)
              this.viewCtrl.dismiss(result);


          }, (err) => {  
            this.msj.showError3('Error',err.error);
          });
          }
     }   
     else
     {
      if(options){
        this.authService.createReg(this._ID,options,'conferencias').then((result) => {
         
          if (!this.form.valid) { return; }
          console.log(result)
          this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}
  }

  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }


  
  


}



